// Создаем новый observer (наблюдатель)
let observer = new IntersectionObserver(function (entries, obs) {
  entries.forEach(function (entry) {
    // Если элемент в зоне видимости, то ничего не происходит
    if (!entry.isIntersecting) return;

    // удаляем классы
    let observerTitles = document.querySelectorAll(".single-service-nav__link");

    observerTitles.forEach((item) => {
      item.classList.remove("single-service-nav__link_active");
      document
        .querySelector(".single-service-nav__link_1")
        .classList.add("single-service-nav__link_active");
    });
  });
});

if (document.querySelector(".service-descr__h3_1")) {
  // Задаем элемент для наблюдения
  let el = document.querySelector(".service-descr__h3_1");
  // Прикрепляем его к «наблюдателю»
  observer.observe(el);
}

