const anchors = document.querySelectorAll('a[href*="#"]');
if (anchors) {
  for (let anchor of anchors) {
    anchor.addEventListener("click", function (e) {
      e.preventDefault();

      const blockID = anchor.getAttribute("href").substr(1);
      if (blockID) {
        document.getElementById(blockID).scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }
    });
  }
}

let observerLinks = document.querySelectorAll(".single-service-nav__link");

if (observerLinks) {
  const btns = document.querySelectorAll(".single-service-nav__link");
  btns.forEach(function (btn) {
    btn.addEventListener("click", function () {
      setTimeout(() => {
        if (!this.classList.contains("acc-active")) {
          btns.forEach((btn) => {
            btn.classList.remove("single-service-nav__link_active");
          });
          this.classList.add("single-service-nav__link_active");
        } else {
          this.classList.remove("single-service-nav__link_active");
        }
      }, "1000");
    });
  });
}

