import Swiper from "swiper/bundle";

const swiperPhoto = new Swiper(".swiper-photo", {
  loop: false,
  slidesPerView: 1,
  spaceBetween: 15,
  navigation: {
    nextEl: ".swiper-button-next-projects",
    prevEl: ".swiper-button-prev-projects",
  },
  breakpoints: {
    767: {
      slidesPerView: 2,
      spaceBetween: 25,
    },
    1239: {
      slidesPerView: 3,
      spaceBetween: 60,
    },
  },
});

if (document.querySelector(".swiper-photo")) {
  if (swiperPhoto.activeIndex == 0) {
    document
      .querySelector(".tabs__title:nth-child(1)")
      .classList.add("tabs__title_active");
  }

  console.log(swiperPhoto.activeIndex);
  let slideTitles = document.querySelectorAll(".tabs__title");
  let slideTitle1 = document.querySelector(".tabs__title:nth-child(1)");
  let slideTitle2 = document.querySelector(".tabs__title:nth-child(2)");
  let slideTitle3 = document.querySelector(".tabs__title:nth-child(3)");
  let slideTitle4 = document.querySelector(".tabs__title:nth-child(4)");
  let slideTitle5 = document.querySelector(".tabs__title:nth-child(5)");

  slideTitle1.addEventListener("click", () => {
    slideTitles.forEach((item) => {
      item.classList.remove("tabs__title_active");
    });
    slideTitle1.classList.add("tabs__title_active");
    swiperPhoto.slideTo(0);
  });
  slideTitle2.addEventListener("click", () => {
    slideTitles.forEach((item) => {
      item.classList.remove("tabs__title_active");
    });
    slideTitle2.classList.add("tabs__title_active");
    swiperPhoto.slideTo(1);
  });
  slideTitle3.addEventListener("click", () => {
    slideTitles.forEach((item) => {
      item.classList.remove("tabs__title_active");
    });
    slideTitle3.classList.add("tabs__title_active");
    swiperPhoto.slideTo(2);
  });
  slideTitle4.addEventListener("click", () => {
    slideTitles.forEach((item) => {
      item.classList.remove("tabs__title_active");
    });
    slideTitle4.classList.add("tabs__title_active");
    swiperPhoto.slideTo(3);
  });
  slideTitle5.addEventListener("click", () => {
    slideTitles.forEach((item) => {
      item.classList.remove("tabs__title_active");
    });
    slideTitle5.classList.add("tabs__title_active");
    swiperPhoto.slideTo(4);
  });

  swiperPhoto.on("slideChange", function () {
    if (swiperPhoto.activeIndex == 0) {
      slideTitles.forEach((item) => {
        item.classList.remove("tabs__title_active");
      });
      slideTitle1.classList.add("tabs__title_active");
    }
    if (swiperPhoto.activeIndex == 1) {
      slideTitles.forEach((item) => {
        item.classList.remove("tabs__title_active");
      });
      slideTitle2.classList.add("tabs__title_active");
    }
    if (swiperPhoto.activeIndex == 2) {
      slideTitles.forEach((item) => {
        item.classList.remove("tabs__title_active");
      });
      slideTitle3.classList.add("tabs__title_active");
    }
    if (swiperPhoto.activeIndex == 3) {
      slideTitles.forEach((item) => {
        item.classList.remove("tabs__title_active");
      });
      slideTitle4.classList.add("tabs__title_active");
    }
    if (swiperPhoto.activeIndex == 4) {
      slideTitles.forEach((item) => {
        item.classList.remove("tabs__title_active");
      });
      slideTitle5.classList.add("tabs__title_active");
    }
    if (
      document
        .querySelector(".swiper-button-next-projects")
        .classList.contains("swiper-button-disabled")
    ) {
      slideTitles.forEach((item) => {
        item.classList.remove("tabs__title_active");
      });
      slideTitle5.classList.add("tabs__title_active");
    }
  });
}
